import { useEffect, useRef, useState } from 'react';
import { MessageCircle, Users, Send, MapPin, Sparkles, TrendingUp, Brain, Menu, X } from 'lucide-react';

type Message = { username: string; text?: string; url?: string; createdat?: number; created_at?: string; sender_name?: string };

export default function ChatPage() {
  const [room, setRoom] = useState('general');
  const [username, setUsername] = useState('');
  const [message, setMessage] = useState('');
  const [messages, setMessages] = useState<Message[]>([
    { username: 'Alice', text: 'Hey everyone! 👋', created_at: new Date().toLocaleTimeString() },
    { username: 'Bob', text: 'Working on the new feature...', created_at: new Date().toLocaleTimeString() },
    { username: 'Charlie', text: 'Looking great!', created_at: new Date().toLocaleTimeString() }
  ]);
  const [users, setUsers] = useState<string[]>(['Alice', 'Bob', 'Charlie', 'Diana']);
  const [connected, setConnected] = useState(true);
  const [sidebarOpen, setSidebarOpen] = useState(false);
  const [aiPanelOpen, setAiPanelOpen] = useState(false);
  const messagesRef = useRef<HTMLDivElement | null>(null);

  const send = () => {
    if (!message.trim()) return;
    setMessages(prev => [...prev, { 
      username: username || 'You', 
      text: message, 
      created_at: new Date().toLocaleTimeString() 
    }]);
    setMessage('');
    setTimeout(() => messagesRef.current?.scrollTo({ top: messagesRef.current.scrollHeight, behavior: 'smooth' }), 50);
  };

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      send();
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-purple-900 via-blue-900 to-indigo-900 flex overflow-hidden relative">
      {/* Animated background elements */}
      <div className="absolute inset-0 overflow-hidden pointer-events-none">
        <div className="absolute top-20 left-20 w-72 h-72 bg-purple-500 rounded-full mix-blend-multiply filter blur-3xl opacity-20 animate-pulse"></div>
        <div className="absolute bottom-20 right-20 w-96 h-96 bg-blue-500 rounded-full mix-blend-multiply filter blur-3xl opacity-20 animate-pulse" style={{animationDelay: '1s'}}></div>
        <div className="absolute top-1/2 left-1/2 w-80 h-80 bg-indigo-500 rounded-full mix-blend-multiply filter blur-3xl opacity-20 animate-pulse" style={{animationDelay: '2s'}}></div>
      </div>

      {/* Left Sidebar - Users & Rooms */}
      <aside className={`${sidebarOpen ? 'translate-x-0' : '-translate-x-full'} md:translate-x-0 fixed md:relative w-80 h-full bg-black/30 backdrop-blur-2xl border-r border-white/10 transition-transform duration-300 z-50`}>
        <div className="p-6 h-full flex flex-col">
          <div className="flex items-center justify-between mb-8">
            <div className="flex items-center gap-3">
              <div className="w-10 h-10 bg-gradient-to-br from-purple-500 to-blue-500 rounded-xl flex items-center justify-center shadow-lg shadow-purple-500/50">
                <MessageCircle className="w-5 h-5 text-white" />
              </div>
              <div>
                <h1 className="text-white font-bold text-lg">ChatFlow</h1>
                <div className="flex items-center gap-2">
                  <div className={`w-2 h-2 rounded-full ${connected ? 'bg-green-400' : 'bg-red-400'} animate-pulse`}></div>
                  <span className="text-xs text-gray-400">{connected ? 'Connected' : 'Offline'}</span>
                </div>
              </div>
            </div>
            <button onClick={() => setSidebarOpen(false)} className="md:hidden text-white/60 hover:text-white">
              <X className="w-5 h-5" />
            </button>
          </div>

          <div className="mb-6">
            <label className="text-xs text-gray-400 uppercase tracking-wider mb-2 block font-semibold">Active Room</label>
            <div className="relative group">
              <input 
                value={room} 
                onChange={e => setRoom(e.target.value)} 
                className="w-full p-3 bg-white/5 border border-white/10 rounded-xl text-white placeholder-gray-500 focus:outline-none focus:border-purple-500 focus:bg-white/10 transition-all duration-300"
                placeholder="Enter room name"
              />
              <div className="absolute inset-0 bg-gradient-to-r from-purple-500/20 to-blue-500/20 rounded-xl opacity-0 group-hover:opacity-100 transition-opacity -z-10 blur"></div>
            </div>
          </div>

          <div className="mb-6">
            <label className="text-xs text-gray-400 uppercase tracking-wider mb-2 block font-semibold">Your Name</label>
            <div className="relative group">
              <input 
                value={username} 
                onChange={e => setUsername(e.target.value)} 
                className="w-full p-3 bg-white/5 border border-white/10 rounded-xl text-white placeholder-gray-500 focus:outline-none focus:border-blue-500 focus:bg-white/10 transition-all duration-300"
                placeholder="Anonymous"
              />
              <div className="absolute inset-0 bg-gradient-to-r from-blue-500/20 to-purple-500/20 rounded-xl opacity-0 group-hover:opacity-100 transition-opacity -z-10 blur"></div>
            </div>
          </div>

          <button className="w-full p-3 bg-gradient-to-r from-purple-600 to-blue-600 text-white rounded-xl font-semibold hover:from-purple-500 hover:to-blue-500 transition-all duration-300 shadow-lg shadow-purple-500/30 hover:shadow-purple-500/50 hover:scale-105 transform">
            Join Room
          </button>

          <div className="mt-8 flex-1 overflow-hidden flex flex-col">
            <div className="flex items-center gap-2 mb-4">
              <Users className="w-4 h-4 text-gray-400" />
              <h3 className="text-xs text-gray-400 uppercase tracking-wider font-semibold">Online ({users.length})</h3>
            </div>
            <div className="flex-1 overflow-auto space-y-2">
              {users.map((u, i) => (
                <div key={u} className="flex items-center gap-3 p-3 rounded-lg bg-white/5 hover:bg-white/10 transition-all duration-300 group cursor-pointer">
                  <div className="w-10 h-10 rounded-full bg-gradient-to-br from-purple-400 to-blue-400 flex items-center justify-center text-white font-semibold shadow-lg">
                    {u.charAt(0)}
                  </div>
                  <div className="flex-1">
                    <div className="text-white text-sm font-medium">{u}</div>
                    <div className="text-xs text-gray-400">Active now</div>
                  </div>
                  <div className="w-2 h-2 bg-green-400 rounded-full"></div>
                </div>
              ))}
            </div>
          </div>
        </div>
      </aside>

      {/* Main Chat Area */}
      <main className="flex-1 flex flex-col relative">
        <div className="bg-black/20 backdrop-blur-xl border-b border-white/10 p-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-4">
              <button onClick={() => setSidebarOpen(true)} className="md:hidden text-white/60 hover:text-white">
                <Menu className="w-6 h-6" />
              </button>
              <div>
                <h2 className="text-white font-bold text-xl">#{room}</h2>
                <p className="text-gray-400 text-sm">{users.length} members active</p>
              </div>
            </div>
            <button onClick={() => setAiPanelOpen(!aiPanelOpen)} className="md:hidden p-2 rounded-lg bg-white/5 hover:bg-white/10 text-white">
              <Brain className="w-5 h-5" />
            </button>
          </div>
        </div>

        <div ref={messagesRef} className="flex-1 overflow-auto p-6 space-y-4">
          {messages.map((m, i) => (
            <div key={i} className="flex gap-3 opacity-0 animate-fadeIn" style={{animationDelay: `${i * 0.05}s`, animationFillMode: 'forwards'}}>
              <div className="w-10 h-10 rounded-full bg-gradient-to-br from-pink-400 to-purple-400 flex items-center justify-center text-white font-semibold shadow-lg flex-shrink-0">
                {(m.username || m.sender_name || 'A').charAt(0)}
              </div>
              <div className="flex-1">
                <div className="flex items-baseline gap-2 mb-1">
                  <span className="text-white font-semibold">{m.username || m.sender_name}</span>
                  <span className="text-xs text-gray-400">{m.created_at || (m.createdat ? new Date(m.createdat).toLocaleTimeString() : '')}</span>
                </div>
                <div className="bg-white/10 backdrop-blur-lg border border-white/20 rounded-2xl rounded-tl-none p-4 shadow-xl hover:bg-white/15 transition-all duration-300">
                  {m.text && <p className="text-white leading-relaxed">{m.text}</p>}
                  {m.url && (
                    <a className="flex items-center gap-2 text-blue-400 hover:text-blue-300 transition-colors" href={m.url} target="_blank" rel="noreferrer">
                      <MapPin className="w-4 h-4" />
                      <span>Shared location</span>
                    </a>
                  )}
                </div>
              </div>
            </div>
          ))}
        </div>

        <div className="p-4 bg-black/20 backdrop-blur-xl border-t border-white/10">
          <div className="flex gap-3 max-w-5xl mx-auto">
            <div className="flex-1 relative group">
              <input 
                value={message} 
                onChange={e => setMessage(e.target.value)} 
                onKeyPress={handleKeyPress}
                className="w-full p-4 bg-white/10 border border-white/20 rounded-2xl text-white placeholder-gray-400 focus:outline-none focus:border-purple-500 focus:bg-white/20 transition-all duration-300"
                placeholder="Type your message..."
              />
              <div className="absolute inset-0 bg-gradient-to-r from-purple-500/10 to-blue-500/10 rounded-2xl opacity-0 group-focus-within:opacity-100 transition-opacity -z-10 blur"></div>
            </div>
            <button 
              type="button" 
              className="p-4 bg-white/10 hover:bg-white/20 border border-white/20 rounded-2xl text-white transition-all duration-300 hover:scale-105 transform"
            >
              <MapPin className="w-5 h-5" />
            </button>
            <button 
              onClick={send}
              className="px-6 py-4 bg-gradient-to-r from-purple-600 to-blue-600 text-white rounded-2xl font-semibold hover:from-purple-500 hover:to-blue-500 transition-all duration-300 shadow-lg shadow-purple-500/30 hover:shadow-purple-500/50 hover:scale-105 transform flex items-center gap-2"
            >
              <Send className="w-5 h-5" />
              <span className="hidden sm:inline">Send</span>
            </button>
          </div>
        </div>
      </main>

      {/* Right Sidebar - AI Insights */}
      <aside className={`${aiPanelOpen ? 'translate-x-0' : 'translate-x-full'} md:translate-x-0 fixed md:relative w-80 h-full bg-black/30 backdrop-blur-2xl border-l border-white/10 transition-transform duration-300 z-50`}>
        <div className="p-6 h-full flex flex-col">
          <div className="flex items-center justify-between mb-6">
            <div className="flex items-center gap-3">
              <div className="w-10 h-10 bg-gradient-to-br from-pink-500 to-purple-500 rounded-xl flex items-center justify-center shadow-lg shadow-pink-500/50">
                <Brain className="w-5 h-5 text-white" />
              </div>
              <h3 className="text-white font-bold text-lg">AI Insights</h3>
            </div>
            <button onClick={() => setAiPanelOpen(false)} className="md:hidden text-white/60 hover:text-white">
              <X className="w-5 h-5" />
            </button>
          </div>

          <div className="space-y-4 flex-1 overflow-auto">
            <div className="bg-gradient-to-br from-purple-500/20 to-pink-500/20 border border-purple-400/30 rounded-2xl p-4 backdrop-blur hover:scale-105 transition-transform duration-300">
              <div className="flex items-center gap-2 mb-3">
                <Sparkles className="w-4 h-4 text-purple-400" />
                <span className="text-xs text-purple-300 uppercase tracking-wider font-semibold">Summary</span>
              </div>
              <p className="text-white text-sm leading-relaxed">
                The conversation is focused on feature development and team collaboration. Activity level is high with positive sentiment.
              </p>
            </div>

            <div className="bg-gradient-to-br from-blue-500/20 to-cyan-500/20 border border-blue-400/30 rounded-2xl p-4 backdrop-blur hover:scale-105 transition-transform duration-300">
              <div className="flex items-center gap-2 mb-3">
                <TrendingUp className="w-4 h-4 text-blue-400" />
                <span className="text-xs text-blue-300 uppercase tracking-wider font-semibold">Activity</span>
              </div>
              <div className="space-y-2">
                <div className="flex justify-between text-sm">
                  <span className="text-gray-300">Messages today</span>
                  <span className="text-white font-semibold">127</span>
                </div>
                <div className="w-full h-2 bg-white/10 rounded-full overflow-hidden">
                  <div className="h-full w-3/4 bg-gradient-to-r from-blue-400 to-cyan-400 rounded-full animate-pulse"></div>
                </div>
              </div>
            </div>

            <div className="bg-gradient-to-br from-green-500/20 to-emerald-500/20 border border-green-400/30 rounded-2xl p-4 backdrop-blur hover:scale-105 transition-transform duration-300">
              <div className="flex items-center gap-2 mb-3">
                <Users className="w-4 h-4 text-green-400" />
                <span className="text-xs text-green-300 uppercase tracking-wider font-semibold">Top Contributors</span>
              </div>
              <div className="space-y-2">
                {['Alice', 'Bob', 'Charlie'].map((name, i) => (
                  <div key={name} className="flex items-center gap-2">
                    <div className="w-6 h-6 rounded-full bg-gradient-to-br from-green-400 to-emerald-400 flex items-center justify-center text-white text-xs font-semibold">
                      {i + 1}
                    </div>
                    <span className="text-white text-sm">{name}</span>
                  </div>
                ))}
              </div>
            </div>

            <button className="w-full p-4 bg-gradient-to-r from-pink-600 to-purple-600 text-white rounded-2xl font-semibold hover:from-pink-500 hover:to-purple-500 transition-all duration-300 shadow-lg shadow-pink-500/30 hover:shadow-pink-500/50 hover:scale-105 transform">
              Generate Full Report
            </button>
          </div>
        </div>
      </aside>

      <style>{`
        @keyframes fadeIn {
          from {
            opacity: 0;
            transform: translateY(10px);
          }
          to {
            opacity: 1;
            transform: translateY(0);
          }
        }
        .animate-fadeIn {
          animation: fadeIn 0.3s ease-out forwards;
        }
      `}</style>
    </div>
  );
}